.printFrame {
  width: 717.4px;
  page-break-after: always;
}

.frame {
  position: relative;
  float: left;
  width: 63mm;
  height: 88mm;
  margin: 0px 0.5px;
}

.card {
  position: absolute;
  width: 63mm;
  height: 88mm;
  z-index: 10;
}

.input {
  position: absolute;
  left: 5px;
  bottom: 7px;
  z-index: 20;
  width: 55mm;
  font-size: 16px;
}

.deleteButton {
  position: absolute;
  right: 0;
  z-index: 30;
  width: 40px;
  height: 35px;
  display: inline-block;
  padding: 0.5em 1em;
  text-decoration: none;
  color: #AA0000;
  border: dashed 1px #FF3333;
  background: #FFD5EC;
  border-radius: 3px;
  -webkit-transition: .4s;
  transition: .4s;
  font-size: 15px;
  opacity: 0.8;
  font-weight: bold;
}

.button-div {
  text-align: center;
}

.addCard {
  display: inline-block;
  padding: 0.5em 1em;
  text-decoration: none;
  background: #668ad8;/*ボタン色*/
  color: #FFF;
  border-bottom: solid 4px #627295;
  border-radius: 3px;
  width: 600px;
  height: 50px;
  font-size: 25px;
}

.addCard:active {
  /*ボタンを押したとき*/
  -webkit-transform: translateY(4px);
  transform: translateY(4px);/*下に動く*/
  border-bottom: none;/*線を消す*/
}

@media print{
    .addCard{
        display: none;
    }
    .input{
        display: none;
    }
    .deleteButton{
        display: none;
    }
}


